import React, { Component } from 'react'

export class TransformButtons extends Component {
    
    render() {
        let {text, handleUpperCase, handleLowerCase, handleTitleCase, handleSentenceCase, handleRemoveExtraSpace, handleClear} = this.props;

        return (
            <div className="container py-3 ">
                <button disabled={text.length===0?true:false} type="button" class="btn primary-btn" onClick={handleUpperCase}>Upper Case</button>
                <button disabled={text.length===0?true:false} type="button" class="btn primary-btn" onClick={handleLowerCase}>Lower Case</button>
                <button disabled={text.length===0?true:false} type="button" class="btn primary-btn" onClick={handleTitleCase}>Title Case</button>
                <button disabled={text.length===0?true:false} type="button" class="btn primary-btn" onClick={handleSentenceCase}>Sentence Case</button>
                <button disabled={text.length===0?true:false} type="button" class="btn primary-btn" onClick={handleRemoveExtraSpace}>Remove Space</button>
                <button disabled={text.length===0?true:false} type="button" class="btn primary-btn" onClick={handleClear}>Clear</button>
            </div>
        )
    }
}

export default TransformButtons
