import React, { Component } from 'react'
import TextHelper from './TextHelper';
import TransformButtons from './TransformButtons'
import WordCounter from './WordCounter'

export class TextBox extends Component {
    constructor() {
        super()
        this.state = {text:""};
    }

    handleOnChange = (event) => {
        this.setState({text:event.target.value});
    }

    handleToUpperCase = () => {
        this.setState({text:this.state.text.toUpperCase()});
    }

    handleToLowerCase = () => {
        this.setState({text:this.state.text.toLowerCase()});
    }

    handleToTitleCase = () => {
        let words = this.state.text.split(" ");
        let capitalWords = [];
        words.forEach(element  => {
            capitalWords.push(element[0].toUpperCase() + element.slice(1,element.length).toLowerCase());
        })
        let titleCase = capitalWords.join(" ");
        this.setState({text:titleCase});
    }

    handleToSentenceCase = () => {
        let tempText = this.state.text;
        this.setState({text:tempText[0].toUpperCase() + tempText.slice(1,tempText.length).toLowerCase()});
    }

    handleRemoveExtraSpace = () => {
        let words = this.state.text.split(/  */);
        this.setState({text:words.join(' ')});
    }

    handleClear = () => {
        this.setState({text:''});
    }

    render() {
        return (
            <div className="container-fluid">
                <TextHelper text = {this.state.text} />
                <div class="container">
                    <textarea 
                        class="form-control text-box" 
                        rows="12" 
                        value={this.state.text} 
                        onChange={this.handleOnChange}>
                    </textarea>
                </div>
                <TransformButtons 
                    text = {this.state.text} 
                    handleUpperCase = {this.handleToUpperCase}
                    handleLowerCase = {this.handleToLowerCase}
                    handleTitleCase = {this.handleToTitleCase}
                    handleSentenceCase = {this.handleToSentenceCase}
                    handleRemoveExtraSpace = {this.handleRemoveExtraSpace} 
                    handleClear = {this.handleClear} 
                />
                <WordCounter text = {this.state.text}/>
            </div>
        )
    }
}

export default TextBox
