import React, { Component } from 'react'

export class WordCounter extends Component {
    render() {
        let {text} = this.props;
        return (
            <div className="container mb-4">
                <div className="word-count">
                    <strong>{text.split(/\s+/).filter((element)=>{return element.length!==0}).length} </strong>
                     words and <strong>{text.split('').length}</strong> characters
                </div>
                <h5 className="mt-3">Reading Time</h5>
                <div className="read-time">
                    <strong>{(text.split(/\s+/).filter((element)=>{return element.length!==0}).length * 0.3 / 60).toPrecision(2)} </strong>
                     Minutes
                </div>
            </div>
        )
    }
}

export default WordCounter
