import React, { Component } from 'react'

export class TextHelper extends Component {
    constructor() {
        super()
        this.copyBtnText = React.createRef();
        this.btnBg = React.createRef();
    }

    render() {
        let {text} = this.props;
        function handleCopy(copyBtnText,btnBg) {
            navigator.clipboard.writeText(text);

            copyBtnText.current.innerHTML = "Copied!";
            btnBg.current.style.width = 0;
            btnBg.current.style.height = 0;

            setTimeout(() => {
                copyBtnText.current.innerHTML = "Copy";
                btnBg.current.style.width = '150px';
                btnBg.current.style.height = '150px';
            }, 1200);
        }
        

        return (
                <div className="container d-flex pt-5" style={{position:"relative"}}>
                    <p className="textbox-helper">Enter or paste your text below</p>
                    <button disabled={text.length===0?true:false} 
                        type="button" class="copy-btn" 
                        onClick={()=>{try{handleCopy(this.copyBtnText,this.btnBg)} catch(err){console.error(err)}}}>
                            <div ref={this.copyBtnText} class="btn-text">Copy</div>
                            <div ref={this.btnBg} class="slidebg"></div>
                    </button>
                </div>
        )
    }
}

export default TextHelper
